import React, {useEffect, useMemo} from 'react';
import {connect} from 'react-redux';
import styled from '@emotion/styled';
import {globalHistory as history} from '@reach/router';

import {
  activateSelection,
  nextSelection,
  previousSelection,
  reset,
  setQuery,
} from '../actions';

import Input from './input';
import Results from './results';

const Form = styled.form`
  margin: 0;
  position: relative;
  width: 100%;
`;

function SearchForm({dispatch, query, results, api, selection}) {
  const onChange = useMemo(() => value => dispatch(setQuery(api, value)), [
    dispatch,
    api,
  ]);

  useEffect(() => history.listen(() => dispatch(reset())), []);

  const onSubmit = useMemo(
    () => event => {
      event.preventDefault();
      return false;
    },
    [],
  );

  const onKeyUp = useMemo(
    () => event => {
      switch (event.which) {
        case 13:
          event.preventDefault();
          dispatch(activateSelection());
          break;
        case 38:
          event.preventDefault();
          dispatch(previousSelection());
          break;
        case 40:
          event.preventDefault();
          dispatch(nextSelection());
          break;

        default:
        // Do nothing
      }
    },
    [dispatch],
  );

  return (
    <Form onSubmit={onSubmit} onKeyUp={onKeyUp}>
      <Input value={query} onChange={onChange} />
      <Results results={results} selection={selection.current} />
    </Form>
  );
}

export default connect(state => state)(SearchForm);
